@import "~@/assets/css/_variables.scss";

.top-toolbar {
  display: flex;
  justify-content: end;
  gap: 16px;
  margin-bottom: 16px;
  .filter-box {
    position: relative;
    display: flex;
    gap: 8px;
    #keyword {
      border: none;
      border-bottom: 1px solid #ccc;
      &::placeholder {
        color: #999;
      }
    }
    > button {
      position: absolute;
      right: 0;
      border: none;
      margin: 0;
      padding: 0;
      background: none;

      > i {
        font-size: $fs;
        transition: color 0.2s;
      }
      &:hover {
        color: $c-main;
      }
    }
  }
  .sort-box {
    #sortButton {
      border: none;
      border-bottom: 1px solid #ccc;
      margin: 0;
      padding: 0;
      background: none;
      span {
        color: #999;
      }
      i {
        font-size: $fs * 0.9;
      }
    }
  }
}
.product-list {
  position: relative;
  .product-item {
    display: flex;
    flex-direction: column;
    border-radius: 4px;
    border: 1px solid #eee;
    padding: 8px;
    height: 100%;
    > *:not(:first-child) {
      margin-top: 8px;
    }
    .img-box {
      position: relative;
      padding-bottom: 70%;
      > * {
        position: absolute;
        width: 100%;
        height: 100%;
      }
      img {
        border-bottom: 1px solid #eee;
        border-radius: 4px;
        object-fit: cover;
      }
      button {
        border: none;
        margin: 0;
        padding: 0;
        background: none;
      }
      .img-null {
        display: flex;
        justify-content: center;
        align-items: center;
        border-radius: 4px;
        color: #ccc;
        font-weight: bold;
        font-size: $fs * 1.5;
        background-color: #f5f5f5;
      }
    }
    .tag-box {
      display: flex;
      gap: 8px;
      > span {
        pointer-events: none;
        color: #fff;
        border-radius: 4px;
        padding: 0 8px;
        font-size: $fs * 0.9;
      }
      .blueprint {
        background-color: #777;
      }
      .legend {
        background-color: #74c1c1;
      }
    }
    .title {
      font-size: $fs * 1.2;
      font-weight: bold;
      color: #555;
    }
    .dcname {
      color: #777;
    }
    .message {
      color: $c-main;
    }
    .order {
      margin-top: auto;
      .price {
        margin-top: 8px;
        text-align: right;
        font-size: $fs * 1.5;
        font-weight: bold;
        color: #555;
        > span {
          font-size: $fs;
          font-weight: normal;
          margin-right: 8px;
        }
      }
      .exchange {
        margin-top: 8px;
        font-weight: bold;
        color: #555;
        > span {
          font-weight: normal;
          color: #777;
        }
      }
      .btn-order {
        display: block;
        width: 100%;
        border-radius: 4px;
        border: 1px solid $c-main;
        margin-top: 8px;
        padding: 4px 0;
        background: linear-gradient(to left, #fff 50%, $c-main 50%) right;
        background-size: 201%;
        color: $c-main;
        transition: color 0.2s, background-position 0.4s;
        &:hover {
          color: #fff;
          background-position: left;
        }
      }
    }
  }
}
.navigation {
  .pagination {
    margin: 32px 0;
    justify-content: end;
    .router-link-active {
      color: #fff;
    }
    .page-item.disabled .router-link-active {
      color: #999;
    }
  }
}
#ProductImgModalDom {
  .modal-dialog {
    width: auto;
    justify-content: center;
    .modal-content {
      width: auto;
      .modal-header {
        background: #eee;
        .modal-title {
          font-weight: bold;
        }
      }
      img {
        width: auto;
        max-width: 100%;
        border-radius: 4px;
      }
    }
  }
}
